import React from 'react';
import Footer from './Footer';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import db from 'libs/db';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';

export default function HealthInsurance(props) {

	const [healthInsurance, setHealthInsurance] = React.useState("");
	const STATE = props.location.state

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const RadioInputOptions = [
		{ key: '1', name: 'Yes' },
		{ key: '0', name: 'No' }
	]

	React.useEffect(async ()=>{
		await db.get(db.model.riskProfile).then(p=>{
			if(p){
				setHealthInsurance(p.healthInsurance || "")
			} else {
				setHealthInsurance("")
			}
		})
		setLoading(false)
	},[])

	const checkValidation = () => {
		if (healthInsurance) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [healthInsurance])

	if (STATE === undefined) {
		props.history.push('/Dashboard')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your risk profile"
				navigation={props.history}
			/>
			<ProgressBar now={54} className="progress-bar-custom" />
			<div className="dataContainer" style={{paddingTop:'42px'}}>
				{
					loading ?
					<ActivityLoaderMintbox />
						:
						<>
							<div className="header1dark">Do you and all your financial dependents have a sufficient health insurance covers?</div>
							{RadioInputOptions.map((obj) => (
								<div key={obj.key} style={{ display: 'flex', flexDirection: 'row' ,marginTop:"6%"}}>
									<div className="radio-item">
										<input
											type="radio"
											id={obj.key}
											name="ritem"
											value={obj.key}
											onChange={(val) => {
												setHealthInsurance(val.target.value)
												console.log(val.target.value)
											}}
											className="bodyTextDark"
											checked={healthInsurance === obj.key}
										/>
										<label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.name}</label>
									</div>
								</div>
							))}
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				nextDisabled={nextDisabled}
				next="/risk-profile/7"
				state={{...STATE,healthInsurance}}
				saveData={{ healthInsurance }}
				buttonText="Confirm"
			/>
		</div>
	)
}
